<template>
    <p-card>
        <template #content>
            <div class="p-fluid p-formgrid p-grid p-input-filled">
                <div class="p-field p-col-12 p-md-1">
                    <label class="p-mb-1">From Date</label>
                    <p-calendar
                        :showIcon="true"
                        v-model="form.fromDate"
                        dateFormat="yy-mm-dd"
                        :class="{
                            'p-invalid': submitted && v$.fromDate.$invalid
                        }"
                    />
                </div>

                <div class="p-field p-col-12 p-md-1">
                    <label class="p-mb-1">To Date</label>
                    <p-calendar
                        :showIcon="true"
                        v-model="form.toDate"
                        dateFormat="yy-mm-dd"
                        :class="{
                            'p-invalid': submitted && v$.toDate.$invalid
                        }"
                    />
                </div>

                <div class="p-field p-col-6 p-md-1">
                    <label class="p-mb-1">-</label>
                    <p-button label="Get List" @click="getOrders" />
                </div>
            </div>
        </template>
    </p-card>

    <p-data-table
        :value="report"
        selectionMode="single"
        dataKey="autoTestOrderId"
        responsiveLayout="scroll"
        showGridlines
        class="p-datatable-sm p-mt-1 p-shadow-1"
        :loading="reportLoading"
        :scrollable="true"
        scrollHeight="calc(100vh - 210px)"
    >
        <p-column header="PROG" field="prog">
            <template #body="{data}">
                <span
                    :style="
                        isStyleRed('PROG', data.prog) ? { color: 'red' } : {}
                    "
                    :class="{ number: !isNaN(data.prog) }"
                    >{{ data.prog }}
                </span>
            </template>
        </p-column>

        <p-column header="BhCG" field="bhcg">
            <template #body="{data}">
                <span
                    :style="
                        isStyleRed('BHCG', data.bhcg) ? { color: 'red' } : {}
                    "
                    :class="{ number: !isNaN(data.bhcg) }"
                    >{{ data.bhcg }}
                </span>
            </template>
        </p-column>
    </p-data-table>
</template>

<script>
import { reactive, ref } from "vue";
import useVuelidate from "@vuelidate/core";
import { required } from "@vuelidate/validators";
import format from "date-fns/format";
import { useStore } from "vuex";
import { THRESHOLD } from "@/composable/daily-report.js";
import { getBhCGList } from "@/service/daily-report";
import { DATE_FORMAT_STRING } from "@/config";
export default {
    setup() {
        const store = useStore();
        const form = reactive({
            fromDate: new Date(),
            toDate: new Date()
        });

        const rules = {
            fromDate: { required },
            toDate: { required }
        };

        const v$ = useVuelidate(rules, form);

        const submitted = ref(false);
        const reportLoading = ref(false);

        const report = ref();

        const getOrders = async () => {
            submitted.value = true;
            if (v$.value.$invalid) return;

            format;

            reportLoading.value = true;
            report.value = await getBhCGList({
                fromDate: format(form.fromDate, DATE_FORMAT_STRING),
                toDate: format(form.toDate, DATE_FORMAT_STRING),
                providerNo: store.state.artCycle.selectedCycle.patientId
            });
            reportLoading.value = false;
        };

        const isStyleRed = (key, value) => {
            if (Number(value) > THRESHOLD[key]) {
                return true;
            } else {
                return false;
            }
        };

        return {
            form,
            v$,
            submitted,
            getOrders,
            report,
            reportLoading,
            isStyleRed
        };
    }
};
</script>

<style scoped>
.p-card :deep() .p-card-content {
    padding: 0 0;
}

.p-field {
    margin-bottom: 0.5rem;
}

.p-card {
    height: 100%;
}
</style>
